import { AppBar, Button, Toolbar } from '@material-ui/core';
import { Link, useLocation } from 'react-router-dom';
import React, { ReactElement, useState } from 'react';
import useStyles from './styles';

interface IMyAppBarProps {
  active: string;
  onButtonClick: (name: string) => () => void;
}

// eslint-disable-next-line react/prop-types
const MyAppBar: ({ active, onButtonClick }: IMyAppBarProps) => ReactElement<IMyAppBarProps> = ({ active, onButtonClick }) => {
  const classes = useStyles();
  const currentRoute = useLocation().pathname;
  const isCurrent = (path: string) => {
    if (currentRoute === path) return 'active';
    return '';
  };
  return (
    <AppBar position={'relative'} className={classes.root}>
      <Toolbar className={classes.toolbarA}>
        <div className={classes.avatarIcon} />
        <div className={classes.toolbarAText}>EL NINO</div>
      </Toolbar>
      <Toolbar className={classes.toolbarB}>
        <Button className={`${classes.Button} ${active === 'aboutme' && 'active'}`} onClick={onButtonClick('aboutme')}>
          About Me
        </Button>
        <Button component={Link} to={'/portfolio'} className={`${classes.Button} ${isCurrent('/portfolio')}`}>
          Portfolio
        </Button>
      </Toolbar>
    </AppBar>
  );
};

export default MyAppBar;
